import comHeader from '@/components/header/header'
import Debounce from '@/util/debounce'
import { getBank } from '@/api/certification'
export default {
  name: 'contents',
  components: {
    comHeader
  },
  props: {},
  data () {
    return {
      realName: '',
      bankName: '中国银行',
      cardNo: '',
      phone: '',
      bankList: [
        '农业银行',
        '工商银行',
        '中国银行',
        '建设银行',
        '招商银行',
        '广发银行',
        '中信银行',
        '上海银行',
        '民生银行'
      ]
    }
  },
  methods: {
    handleSave () {
      const rules = {
        realName: {
          name: "持卡人姓名",
          type: "validName",
          value: this.realName,
          required: !0
        },
        tel: {
          name: "手机号",
          type: "validMobile",
          value: this.phone,
          required: !0
        },
        cardNo: {
          name: "银行卡号",
          type: "validBackNumber",
          value: this.cardNo,
          required: !0
        }
      }
      if (!this.$utils.dataValidityCheck(rules)) return
      Debounce(() => this._onSave(), 1000)
    },
    async _onSave () {
      let params = {
        realName: this.realName,
        bankName: this.bankName,
        cardNo: this.cardNo, // 6217730809694670
        phone: this.phone,
        type: '1'
      }
      getBank(params).then(({ data: { code, data, msg } }) => {
        if (code === 1) {
          this.$vux.toast.text('保存成功')
          this.$router.back(-1)
        }
      }).catch(() => {
        this.$vux.toast.text('网络出错，请稍后重试')
      })
    }
  }
}



